import React, { useEffect, useRef } from 'react';
import './search-component.css'

export function SearchComponent(props) {

    let selector = useRef(null)

    function search(searchedText){

        document.querySelectorAll('div.streamer-component').forEach ( e => {
            const findIt = (element) => {
                if(searchedText.length >= 3 && element.innerText.includes(searchedText)){
                    return true
                } else {
                    return false
                }
            }

            if(Array.from(e.querySelectorAll('div.logoSide span')).some(findIt)) {
                e.classList.add('found')
            } else {
                e.classList.remove('found')
            }

        })
        
    }

    useEffect(() => {
        selector.current.addEventListener('input',(e)=>{
            search(e.target.value)
        })
    })

    return (
        <div 
            className = 'searchDiv'
            ref =  {selector}
        >
            <input className = 'searchBox' type='text' spellCheck="false"/>
        </div>
    );
}
  