import React from 'react';
import { StreamerComponent } from 'components/streamer-component/streamer-component';
import axios from 'axios';
import './board-component.css'

class BoardComponent extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      isLoading: true
    }
    this.myRef = React.createRef()
  }

  setupTick(that){
    that.requestTimer = setInterval(
      () => that.tick(),
      60000
    );
  }

  componentDidMount(){
    let that = this

    this.requestAllData(this.setupTick)

    this.handleResize = () => {
      let scaleRatio = that.myRef.current.clientWidth/2560
      console.log(scaleRatio)
      that.myRef.current.style.setProperty('--base-scale',scaleRatio)
      if(scaleRatio < 0.5){
        that.myRef.current.style.setProperty('--border-scale','1px')
      } else if(scaleRatio > 1) {
        that.myRef.current.style.setProperty('--border-scale','3px')
      } else {
        that.myRef.current.style.setProperty('--border-scale','2px')
      }
    }
    
    window.addEventListener('resize', this.handleResize);
    this.handleResize()

  }

  componentWillUnmount() {
    clearInterval(this.requestTimer);
    window.removeEventListener('resize', this.handleResize);
  }

  tick(){
    this.requestStreamersData()
  }

  requestAllData(callback_){
    const that = this
    let url = './data/allData.json'
    axios.get(url, {})
    .then(function (response) {
      console.log(response)
      if(response.status === 200) {
          that.setState({"streams": response.data.streams})
          that.setState({"games": response.data.games})
          that.setState({"users": response.data.users})
          that.setState({"queryNumber": response.data.queryNumber})
          that.setState({"updateDate": response.data.upDate})
          let currentTime = new Date(response.data.upDate)
          console.log("update time ",currentTime.toLocaleString());
          console.log('query number', response.data.queryNumber)
          that.setState({ isLoading: false });
          callback_(that)
      }
    })
    .catch(function (error) {
        console.log(error);
    })
    .then(function () {
    }); 
  }

  requestStreamersData(){
    const that = this
    let url = './data/streamGameData'+((this.state.queryNumber+1)%11)+'.json'
    axios.get(url, {})
    .then(function (response) {
      console.log(response)
      if(response.status === 200) {
        if(response.data.upDate >= that.state.upDate){
          that.setState({"streams": response.data.streams})
          that.setState({"games": response.data.games})
          that.setState({"queryNumber": response.data.queryNumber})
          that.setState({"updateDate": response.data.upDate})
          let currentTime = new Date(response.data.upDate)
          console.log("update time ",currentTime.toLocaleString());
          console.log('query number', response.data.queryNumber)
          that.setState({ isLoading: false });
        }
      }
    })
    .catch(function (error) {
        console.log(error);
    })
    .then(function () {
    }); 
  }

  generateStreamerComponents(){
    const componentList = this.state.users.data.map(e => {
      let stream = this.state.streams.data.find(f => f.user_id === e.id)
      let position = this.state.users.positions[e.login]
      if(stream){
        let game = this.state.games[stream.game_id]
        return <StreamerComponent key={e.id} user={e} stream={stream} game={game} position={position} />
      } else {
        return <StreamerComponent key={e.id} user={e} position={position}/>
      }
      
    })
    return componentList
  }

  render() {
    const { isLoading } = this.state;

    if (isLoading) {
      return (
        <div className="board-component" ref = {this.myRef}>
          Loading...
        </div>
      );
    }

    return (
      <div 
        className = 'board-component'
        ref = {this.myRef}
      >
      {this.generateStreamerComponents()}
      </div>
    );
  }


}

export { BoardComponent }
