import React, { useState, useEffect, useRef } from 'react';
import './streamer-component.css'
import {ReactComponent as ViewerSvg} from './viewer.svg'

export function StreamerComponent(props) {
  let isStreaming = (props.stream?true:false)
  let currentlyPlaying = (props.game?(props.game.name):"En jeu")
  let selector = useRef(null)
  let openTimeout = null
  let openTime = 250
  const [isDetailed, setDetailed] = useState(0);


  function openTwitch() {
    var win = window.open('https://www.twitch.tv/'+props.user.display_name, '_blank');
    win.focus();
  }

  function clickHandler(){
    selector.current.classList.add('detailedStream');
    setDetailed(true)
  }

  function  onMouseLeaveDiv(){
    selector.current.classList.remove('detailedStream')
    setDetailed(false)
  }

  function generatePreview(){
    let thumbHeight = 360
    let thumbWidth  = 640
    if(selector.current && selector.current.classList.contains('detailedStream')){
      if(isStreaming){
        return(
          <React.Fragment>
            <img src = {props.stream.thumbnail_url.replace('{width}',thumbWidth).replace('{height}',thumbHeight)} className = 'streamPreview'/>
            <div className = 'streamFooter'>
              <div className = 'titleDiv'>
                <span className = 'streamTitle'>{props.stream.title}</span>
              </div>
              <span className = 'streamViewers'><span className = 'viewerCount'>{props.stream.viewer_count}</span><ViewerSvg/></span>
            </div>
          </React.Fragment>
        )
      } else {
        return(
          <img src = {props.user.offline_image_url.replace('1920',thumbWidth).replace('1080',thumbHeight)} className = 'streamPreview' />
        )
      }
    } 
  }

  useEffect(() => {

    if(('ontouchstart' in window) || (navigator.MaxTouchPoints > 0) || (navigator.msMaxTouchPoints > 0)) {
      openTime = 0
    }
    
    selector.current.style.setProperty('--posx',props.position.x+'vw');
    selector.current.style.setProperty('--posy',props.position.y*0.5+'vw');
    selector.current.style.setProperty('--opacity', 1);
    if(props.position.x >= 55) selector.current.classList.add('rightClamp');
    if(props.position.y >= 55) selector.current.classList.add('bottomClamp'); 
    let animationTime = window.getComputedStyle(selector.current).getPropertyValue('--open-animation-time')
    
    setTimeout(function(){
      if(selector.current.querySelector('.titleDiv')){
        let containerHeight = selector.current.querySelector('.titleDiv').offsetHeight
        let titleHeight = selector.current.querySelector('.streamTitle').offsetHeight
        if(containerHeight < titleHeight) {
          selector.current.querySelector('.streamTitle').classList.add('addScroll')
          selector.current.querySelector('.streamTitle').style.setProperty('--animation-size', (((titleHeight/containerHeight)-1)*-100)+'%')
        }
      }
    }, parseFloat(100 + (animationTime.replace('s',''))*1000));

  });
  // <LiveComponent isHovered = {isHovered}/>   
  /*

  
  */

  return (
    <React.Fragment>
      <div id = {props.user.id}
      ref =  {selector}
      className = { `streamer-component ${(isStreaming?'live':'offline')}` }  
      onClick = {() => {
        
      }}
      onMouseEnter={() => {
        openTimeout = setTimeout(() => {
          clickHandler()
        },openTime)
      }}
      onMouseLeave = {() => {
        onMouseLeaveDiv()
        clearTimeout(openTimeout)
      }}
      >
        <div className = 'streamProps'>
          <img className = 'logo' src = {props.user.profile_image_url} alt = 'streamer logo' onClick={() => { openTwitch() }}/>
          <div className = 'logoSide'>
            <span className = 'name'>{props.user.display_name}</span>
            <span className = 'islive'>{(isStreaming?currentlyPlaying:'Hors ligne')}</span>
          </div>
        </div>
        {generatePreview()}
      </div>
     
    </React.Fragment>
  ); 
}
